/**
* Utility classes
*/
/**
* Wordpress image alignment
*/
.alignright {
  float: none; }
  @media screen and (min-width: 768px) {
    .alignright {
      float: right; } }

.alignleft {
  float: none; }
  @media screen and (min-width: 768px) {
    .alignleft {
      float: left; } }

.aligncenter {
  margin: 0 auto;
  max-width: 100%; }

.show-desktop-lg {
  display: none !important; }
  @media only screen and (min-width: 1200px) {
    .show-desktop-lg {
      display: flex !important; } }

.show-mobile {
  display: block; }
  @media only screen and (min-width: 1200px) {
    .show-mobile {
      display: none; } }

.icon {
  display: inline-block;
  height: 1rem;
  width: 1rem; }

/**
* Site-wide layout styling
*/
html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  position: relative; }

body {
  padding-top: 61px; }
  @media only screen and (min-width: 768px) {
    body {
      padding-top: 97px; } }

* {
  box-sizing: border-box; }

img {
  display: block; }

section {
  position: relative;
  width: 100%; }
  section::after {
    clear: both;
    content: "";
    display: block; }

.container {
  padding-right: 16px;
  padding-left: 16px;
  margin-right: auto;
  margin-left: auto; }
  @media only screen and (min-width: 1024px) {
    .container {
      margin-right: auto;
      margin-left: auto;
      max-width: 1200px;
      width: 100%; } }

input::-webkit-input-placeholder {
  line-height: normal !important; }

/**
BUTTONS
button styles
**/
.button {
  font-size: 18px;
  line-height: 21px;
  padding: 16px 32px;
  background: #655DFF;
  border: 2px solid transparent;
  border-radius: 4px;
  color: #FFFFFF;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(1, 0, 0, 1); }
  .button:hover, .button:focus {
    background: #FFFFFF;
    border: 2px solid #655DFF;
    color: #655DFF; }
  .button--small {
    font-size: 16px;
    line-height: 16px;
    padding: 8px 16px; }
  .button--invert {
    color: #655DFF;
    background: #FFFFFF;
    border: 2px solid #655DFF; }
    .button--invert:hover, .button--invert:focus {
      background: #655DFF;
      border: 2px solid transparent;
      color: #FFFFFF; }

/**
EXPERTS
experts component
**/
.experts {
  margin-bottom: 48px; }
  @media only screen and (min-width: 768px) {
    .experts {
      margin-bottom: 110px; } }
